import React, { Component } from 'react';
import './App.css';


class Marker2 extends Component {
    componentDidMount(){
        this.enterAnimation();
    }

    enterAnimation(){
        let marker2 = document.querySelector(".marker2");

        setTimeout(function(){
            marker2.style.right="-10px";
            marker2.style.transition="0.2s ease"
        }, 1);
    }

    render() {
        return (
            <div className="marker2"/>
        );
    }
}

export default Marker2;



